import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/v-710-psqls';

export const apiV710psqlGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiV710psqlPost = async (serviceUrl, v710psql) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: v710psql ? JSON.stringify(v710psql) : null,
  };
  return request(url, options);
};

export const apiV710psqlPut = async (serviceUrl, id, v710psql) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: v710psql ? JSON.stringify(v710psql) : null,
  };
  return request(url, options);
};

export const apiV710psqlDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
